@import "src/theme";


.img {
    font: optional;
    width: 75%;
    height: auto;
}

.details {
    background: $transparent-color;

    .announcement {
        font-family: $header-font-family;
        font-size: 2em;
    }

    .details {
        font-family: $body-font-family;
        font-size: 1.5em;
    }

    .location {
        font-family: $body-font-family;
        font-weight: 100;
        a:link,
        a:visited,
        a:hover,
        a:link {
            color: $font-color;
        }
    }

    .hashtag {
        font-family: $primary-font-family;
        font-weight: 300;
        font-size: 1.1em;
    }
}
