@import "src/theme";

.header {
    height: 300px;
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
    background: $transparent-color;

    padding-top: 165px;
    // padding-bottom: 0 !important;

    h1 {
        font-family: $header-font-family;
        color: $font-color;
        font-weight: bold;
        font-size: 3.5em;
    }

    h2 {
        font-family: $header-font-family;
        color: $font-color;
        font-weight: bold;
        font-size: 1.5em;
    }
}
